import React from "react";
import Header from "./header/Header";
import "./hero.css";
import hero from "../images/hero.png";
import back from "../images/back.png";
import love from "../images/love.png";
import calories from "../images/calories.png";
import { motion } from "framer-motion";
import NumBerCounter from "number-counter";

const Hero = () => {
  const transition = { type: "spring", duration: 3 };
  const mobile = window.innerWidth <= 768 ? true : false;
  return (
    <div className="hero" id="home">
      <div className="left">
        <Header />
        <div className="best_add">
          <motion.div
            initial={{ left: mobile ? "170" : "238px" }}
            whileInView={{ left: "8px" }}
            transition={{ ...transition, type: "tween" }}
          ></motion.div>
          <span className="add">THE BEST FITNESS CLUB IN THE TOWN</span>
        </div>

        <div className="hero__text">
          <div>
            <span className="stroke-text">SHAPE </span>
            <span>YOUR</span>
          </div>
          <div>
            <span>IDEAL BODY</span>
          </div>
          <div>
            <span>
              In here we will help you to shape and build your ideal body and
              live up your life to fullest
            </span>
          </div>
        </div>
        <div className="figures">
          <div>
            <span>
              <NumBerCounter end={120} delay="4" start={60} preFix={"+"} />
            </span>
            <span>EXPERT COACHES</span>
          </div>
          <div>
            <span>
              <NumBerCounter end={1000} delay="5" start={946} preFix={"+"} />
            </span>
            <span>MEMBERS JOINED</span>
          </div>
          <div>
            <span>
              <NumBerCounter end={50} delay="4" start={10} preFix={"+"} />
            </span>
            <span>FITNESS PROGRAMS</span>
          </div>
        </div>

        <div className="hero__buttons">
          <button className="btn">Get Started</button>
          <button className="btn">Learn More</button>
        </div>
      </div>
      <div className="right">
        <button className="btn">Join Now</button>

        <motion.div
          initial={{ right: "-1rem" }}
          whileInView={{ right: "4rem" }}
          transition={transition}
          className="heart__rate"
        >
          <img src={love} alt="" />
          <span>Heart Rate</span>
          <span>116 bpm</span>
        </motion.div>

        <img src={hero} alt="" className="hero__image" />
        <motion.img
          src={back}
          alt=""
          initial={{ right: "11rem" }}
          whileInView={{ right: "20rem" }}
          transition={transition}
          className="hero__image__back"
        />

        <motion.div
          initial={{ right: "37rem" }}
          whileInView={{ right: "28rem" }}
          transition={transition}
          className="calories"
        >
          <img src={calories} alt="" />
          <div className="">
            <span>Calories Burned</span>
            <span>276 Kcal</span>
          </div>
        </motion.div>
      </div>
    </div>
  );
};

export default Hero;
