import React, { useState } from "react";
import "./testimonial.css";
import TesimonialsData from "../data/TesimonialsData";
import arrow1 from "../../images/Arrow.png";
import arrow2 from "../../images/Arrow_r.png";
import { motion } from "framer-motion";

const Testimonials = () => {
  const [selected, setSelected] = useState(0);
  const length = TesimonialsData.length;
  const transition = { type: "spring", duration: 3 };
  return (
    <div className="Testimonials" id="Testimonials">
      <div className="left__t">
        <span>TESTIMONIALS</span>
        <span className="stroke-text">WHAT THEY</span>
        <span>SAY ABOUT US</span>
        <motion.span
          key={selected}
          initial={{ opacity: 0, x: -100 }}
          animate={{ opacity: 1, x: 0 }}
          exit={{ opacity: 0, x: 100 }}
          transition={transition}
          src={TesimonialsData[selected].image}
          alt=""
        >
          {TesimonialsData[selected].review}
        </motion.span>

        <span>
          <span style={{ color: "var(--orange)" }}>
            {TesimonialsData[selected].name}
          </span>{" "}
          - {TesimonialsData[selected].status}
        </span>
      </div>
      <div className="right__t">
        <motion.div
          initial={{ opacity: 0, x: -100 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ ...transition, duration: 2 }}
        ></motion.div>
        <motion.div
          initial={{ opacity: 0, x: 100 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ ...transition, duration: 2 }}
        ></motion.div>
        <motion.img
          key={selected}
          initial={{ opacity: 0, x: 100 }}
          animate={{ opacity: 1, x: 0 }}
          exit={{ opacity: 0, x: -100 }}
          transition={transition}
          src={TesimonialsData[selected].image}
          alt=""
        />
        <div className="arrows">
          <img
            src={arrow1}
            alt=""
            onClick={() =>
              selected === 0
                ? setSelected(length - 1)
                : setSelected((prev) => prev - 1)
            }
          />
          <img
            src={arrow2}
            alt=""
            onClick={() =>
              selected === length - 1
                ? setSelected(0)
                : setSelected((prev) => prev + 1)
            }
          />
        </div>
      </div>
    </div>
  );
};

export default Testimonials;
