import React, { useState } from "react";
import "./header.css";
import logo from "../../images/UrbanFitLogo.png";
import bars from "../../images/bars.png";
import { Link } from "react-scroll";

const Header = () => {
  const mobile = window.innerWidth <= 768 ? true : false;
  const [openMenu, seOpenMenu] = useState(false);

  return (
    <div className="header">
      <img src={logo} alt="" />
      {openMenu === false && mobile === true ? (
        <div
          style={{
            backgroundColor: "var(--appColor)",
            padding: "0.5rem",
            borderRadius: "5px",
          }}
          onClick={() => seOpenMenu(true)}
        >
          <img
            src={bars}
            alt=""
            style={{ width: "1.5rem", height: "1.5rem" }}
          />
        </div>
      ) : (
        <ul className="header__menu">
          <li>
            <Link
              to="home"
              span={false}
              smooth={true}
              onClick={() => seOpenMenu(false)}
            >
              Home
            </Link>
          </li>
          <li onClick={() => seOpenMenu(false)}>
            <Link
              to="programes"
              span={false}
              smooth={true}
              onClick={() => seOpenMenu(false)}
            >
              Programs
            </Link>
          </li>
          <li onClick={() => seOpenMenu(false)}>
            <Link
              to="why"
              span={false}
              smooth={true}
              onClick={() => seOpenMenu(false)}
            >
              Why Us
            </Link>
          </li>
          <li onClick={() => seOpenMenu(false)}>
            <Link
              to="plans"
              span={false}
              smooth={true}
              onClick={() => seOpenMenu(false)}
            >
              Plans
            </Link>
          </li>
          <li>
            <Link
              to="Testimonials"
              span={false}
              smooth={true}
              onClick={() => seOpenMenu(false)}
            >
              Testimonials
            </Link>
          </li>
        </ul>
      )}
    </div>
  );
};

export default Header;
